const Table = (props) => {
    const keyNames = [];

    props.Array.map((row) => {
        const AllKeys = Object.keys(row);
        AllKeys.forEach(key => {
            switch(key) {
                case "0":
                case "period":
                case "reference":
                    return 
                default:
                    return keyNames.push(key);
            }
        });
    });

    console.log("Table");
    return <table>
        <thead>
            <tr>
                <th>{Title(props.Title)}</th>
            </tr>
            <tr>
                {keyNames.map((key, i) => {
                    return <th key={i}>{Title(key)}</th>
                })}
            </tr>
        </thead>
        <tbody>
            {props.Array.map((row, index) => {
                return <tr key={index}>
                    {keyNames.map((key, i) => {
                        switch(true) {
                            case (typeof row[key] === "string"):
                                return <td key={i}>{row[key]}</td>
                            case (typeof row[key] === "array"):
                                return <td key={i}>
                                    <Table
                                        Title={Title(key)}
                                        Array={row[key]}
                                    />
                                </td>
                            case (typeof row[key] === "object"):
                                return <td key={i}>
                                        <Table
                                            Title={Title(key)}
                                            Array={[row[key]]}
                                        />
                                    </td>
                            default:
                                return
                        }
                    }
                    )}
                </tr>
            })}
        </tbody>
    </table>
}

function Title(key) {
    let title = key.charAt(0).toUpperCase() + key.slice(1);
    return title.replace(/([A-Z])/g, ' $1').trim()
}

export default Table